<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-top">
				<ctitle :title="'一级内容分类政策分布'" class="w-l-title"></ctitle>
        <div class="echarts">
					<BarLine :params="chartParams"></BarLine>
        </div>
      </div>
      <div class="w-bottom">
				<div class="b-left">
					<ctitle :title="'信坊量月度走势'"></ctitle>
					<lineMonth :params="zsLineParams"></lineMonth>
				</div>
				<div class="b-right">
					<ctitle :title="'政策分类分布'"></ctitle>
					<div class="echarts">
						<BarLine :params="BarParams"></BarLine>
					</div>
				</div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
	import ctitle from "@/components/title/index";
	// 双轴
	import BarLine from "@/components/bar/BarLine";
	import lineMonth from "@/components/line/lineMonth";
  export default {
    name: 'zctj',
    components: {
			ctitle,
			BarLine,
			lineMonth
    },
    data() {
      return {
				// 一级内容分类政策分布
				chartParams: {
					data: [],
					canvasStyle: {
						width: "100%",
						height: "100%"
					},
					options: {
						barWidth: "40%",
						colors: {
							bar: [[ "#6257e4", "#5cd8ff"]],
							line: [["#fac701", "#fa772c"]],
						},
						map: {
							xfljc: "信访量件次",
							zcsl: "政策数量",
						},
						symbolSize: 2,
						line: ["xfljc"],
						xName: "name",
						twoYAxis: true,
						axisValueShow: true,
						axis1Name: "",
						axis2Name: "",
						legendShow: true,
						barBorderRadius: 5,
						smooth: true,
						left: "5%",
						right: "5%",
						top: "15%",
						bottom: "0%",
						barLabelShow: false,
						lineLabelShow: false,
						categoryBr: 1,
						yaxisLabelColor: "#398acd",
						xAxisLabelColor: "#fff",
						ghjsTooltip: true,
						twoYAxis: true,
						axisLabelSize: 15
					},
				},
				// 信坊量月度走势
				zsLineParams: {
          data: [],
          colors: [
            // 紫
            {
              line: {
              //   type: 'linear',
                x: 0,
                y: 0,
                x2: 1,
                y2: 0,
                colorStops: [{
                  offset: 0,
                  color: '#6356fb'
                },
                 {
                  offset: 0.4,
                  color: '#ed9fcc'
                }, 
                {
                  offset: 1,
                  color: '#9f75e7'
                }]
              },
              start: "rgba(181, 127, 182, 0.3)",
              end: "rgba(181, 127, 182, 0)"
            },
          ],
          option: {
            splitLineShow: true,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "月",
            axisYName: "",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: false,
            areaShow: 1,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "15%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "15%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "数量",
          },
          xName: "name"
				},
				// 政策分类分布
				BarParams: {
          data: [],
          canvasStyle: {
            width: "90%",
            height: "90%"
          },
          options: {
						yAxisColor: '#cdefff',
            xName: "name", //x轴字段
            axisValueShow: true,
            splitLineShow: false, //value轴splitline是否显示
            axis1Name:"",
            axisLabelSize: "14%", // label字号
            left: "6%",
            right: "5%",
            top: "5%",
            bottom: "10%",
            barWidth: 55,
            barLabelOptions: "top",
            barLabelShow: true, //柱状图数量值显示
            // itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#d2f69d", "#79cc7a"]]
            },
            map: {
              value: "次数"
            }
          }
        },
      }
		},
		mounted() {
			this.getAPI()
		},
		methods: {
			getAPI() {
				// 1.一级内容分类政策分布
				get_http("zcglfxZctjYjnrflzcfb", {
        })
				.then(res => {
					this.chartParams.data = res.data.data;
					// this.mapFlag = true;
				})
				.catch(err => {
					console.log(err);
				})
				// 2.信坊量月度走势
				get_http("zcglfxZctjXflydzs", {
				})
				.then(res => {
					this.zsLineParams.data = res.data.data;
				})
				.catch(err => {
					console.log(err);
				});
				// 3.政策分类分布
				get_http("zcglfxZctjZcflfb", {
				})
				.then(res => {
					this.BarParams.data = res.data.data;
				})
				.catch(err => {
					console.log(err);
				});
			}
		}
  }
</script>

<style lang="stylus" scoped>
.wrapper
	position relative
	top 0.45rem
	height calc(100% - 45px)
	.content
		width 100%
		height 100%
		display flex
		padding .2rem .3rem
		box-sizing border-box
		flex-direction column
		.w-top
			flex 1
			.echarts
				width 100%
				height 100%
		.w-bottom
			flex 1
			display flex
			.b-left
				flex 1
			.b-right
				flex 1
				.echarts
					width 100%
					height 100%
</style>